body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f0f0f0; 
}

.App-input {
  text-align: center;
  margin-bottom: 20px;
}

h1 {
  color: #333;
  text-align: center;
}

input[type='text'] {
  padding: 8px;
  margin-right: 10px;
  border: 2px solid #ccc;
  border-radius: 4px;
}

button {
  padding: 8px 16px;
  background-color: #00ffaa;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
button:hover{
  background-color: rgb(88, 214, 88);
  color: #333;
}

nav {
  background-color: #bb00ff; 
  padding: 5px ;
align-items: center;
}

nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}

nav ul li {
  display: inline;
  margin-right: 20px;
}

nav ul li.navActive {
  font-weight: bold;
}
